@use '../mixins/index' as *;
:root {
  --font: "Montserrat", sans-serif;
}

html {
  font-family: var(--font);
}

h1 {
  font-size: 2rem;
}

h2,
h3 {
  color: var(--primary-colour);
  letter-spacing: 2px;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: var(--primary-colour);
  font-weight: 700;
  transition: all 0.3s ease-in-out;

  @include mobile(small) {
    font-size: 1.2rem;
  }
}

li {
  list-style: none;
}

p {
  color: var(--secondary-colour);
}
